import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Modal from '@material-ui/core/Modal';
import './ProjectCard.css'

const styles = theme => ({
   
    media: {
        height: 140,
        backgroundColor: '#e0e0e0'
    },
    paper: {
        position: 'absolute',
        width: theme.spacing.unit * 70,
        maxWidth: '80%',
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4,
        maxHeight : '70vh',
        
        overflowY :'auto'
    },

});
function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}
class ProjectCard extends Component {
    state = {
        open: false,
    };

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    render() {
        const { classes, image_id, info, title, homepage,skills_developed , cargo } = this.props
        return (
            <div className="projectCard">
                <Card className="card" onClick={this.handleOpen} >
                    <CardActionArea className="card">
                        <CardMedia
                            className={classes.media}
                            image={image_id}
                            title={title}

                        />

                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {title}
                            </Typography>
                            <Typography component="p">
                                {info}
                            </Typography>
                        </CardContent>

                    </CardActionArea>

                </Card>


                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                    
                >
                    <div style={getModalStyle()} className={classes.paper} >
                        <Typography variant="h6" id="modal-title">
                           {title}
                           <hr></hr>
                         </Typography>
                        <Typography variant="" id="simple-modal-description">
                            {info} 
                            <hr/>
                        </Typography>
                        <Typography variant="" id="">
                            <h4>Herramientas de desarrollo:</h4>
                            {skills_developed} 
                            <hr/>
                        </Typography>
                        <Typography variant="" id="">
                            <h4>Responsabilidad:</h4>
                            {cargo} 
                            <hr/>
                        </Typography>
                        <div style={{ backgroundColor: 'white' }}>
                            <h4>Sitio web: <a href={homepage} rel="noopener noreferrer" target="_blank">{title}</a> </h4> 
                            </div>
                            
                    </div>
                </Modal>
            </div>

        )
    }
}

ProjectCard.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ProjectCard);