import React, {Component} from 'react'
import {Grid} from '@material-ui/core'
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import './Skills.css'

am4core.useTheme(am4themes_animated);

class Skills extends Component{

    componentDidMount() {
        let chart = am4core.create("chartdiv", am4charts.XYChart);
    
        chart.paddingRight = 20;
    
        chart.data = [{
            "name": "REACT",
            "points": 70,
            "color": chart.colors.next(),
            "bullet": "https://camo.githubusercontent.com/98a9b62f324b8a13275cc57dc4293f0ee315f85f/68747470733a2f2f73616e6473746f726d2e64652f5f5265736f75726365732f50657273697374656e742f333238353431366538353033623263383335346333323162636436393063663535306238623264332f52656163742d4c6f676f2e737667"
        }, {
            "name": "REST",
            "points": 50,
            "color": chart.colors.next(),
            "bullet": "https://miro.medium.com/max/790/1*uHzooF1EtgcKn9_XiSST4w.png"
        }, {
            "name": "Javascript",
            "points": 70,
            "color": chart.colors.next(),
            "bullet": "https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/1024px-Unofficial_JavaScript_logo_2.svg.png"
        }, {
            "name": "HTML 5",
            "points": 85,
            "color": chart.colors.next(),
            "bullet": "https://vignette.wikia.nocookie.net/eswikia/images/f/f0/HTML5-logo.png/revision/latest?cb=20120709105013"
        },
        {
            "name": "CSS3",
            "points": 80,
            "color": chart.colors.next(),
            "bullet": "https://vignette.wikia.nocookie.net/howtoprogram/images/a/a9/CSS3.png/revision/latest?cb=20130422012035"
        },
        {
            "name": "JQuery",
            "points": 60,
            "color": chart.colors.next(),
            "bullet": "http://pluspng.com/img-png/jquery-logo-png--800.gif"
        },
        {
            "name": "Laravel",
            "points": 75,
            "color": chart.colors.next(),
            "bullet": "http://1.bp.blogspot.com/-ikwaeoTr448/VhLWXjFxg-I/AAAAAAAADNg/_NlH18n99yU/s1600/laravel-1.png"
        },
  
    ];

        let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
        categoryAxis.dataFields.category = "name";
        categoryAxis.renderer.grid.template.disabled = true;
        categoryAxis.renderer.minGridDistance = 30;
        categoryAxis.renderer.inside = false;
        categoryAxis.renderer.labels.template.fill = am4core.color("#fff");
        categoryAxis.renderer.labels.template.fontSize = 20;
        
        let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
        valueAxis.renderer.grid.template.strokeDasharray = "4,4";
        valueAxis.renderer.labels.template.disabled = true;
        valueAxis.min = 0;
        
        // Do not crop bullets
        chart.maskBullets = false;
        
        // Remove padding
        chart.paddingBottom = 0;
        
        // Create series
        let series = chart.series.push(new am4charts.ColumnSeries());
        series.dataFields.valueY = "points";
        series.dataFields.categoryX = "name";
        series.columns.template.propertyFields.fill = "color";
        series.columns.template.propertyFields.stroke = "color";
        series.columns.template.column.cornerRadiusTopLeft = 15;
        series.columns.template.column.cornerRadiusTopRight = 15;
        series.columns.template.tooltipText = "{categoryX}: [bold]{valueY}[/b]%";
        
        // Add bullets
        let bullet = series.bullets.push(new am4charts.Bullet());
        let image = bullet.createChild(am4core.Image);
        image.horizontalCenter = "middle";
        image.verticalCenter = "bottom";
        image.dy = 20;
        image.y = am4core.percent(100);
        image.propertyFields.href = "bullet";
        image.tooltipText = series.columns.template.tooltipText;
        image.propertyFields.fill = "color";
        image.filters.push(new am4core.DropShadowFilter());
    }

    componentWillUnmount() {
        if (this.chart) {
          this.chart.dispose();
        }
      }


    render(){
        return(
            <div className="skills">
            <h2>Skills</h2>
                <hr />
                <Grid container spacing={0} > 
                    <Grid item xs={12} md={12}>
                    <div id="chartdiv" style={{ width: "100%", height: "300px" }}></div>
                    </Grid>
                </Grid>
            </div>
        )
    }
    
}


export default Skills